@import "../../variables";

.form_summary_error {
  border: 2px solid orangered;
  padding: 5px;
  color: orangered;
  width: 10.4%;
}

.login {
  margin-top: 2%;
  margin-left: 35%;
  margin-right: 10px;

  h1 {
    margin-left: 80px;
  }

  .input {
    margin-top: 10px;

    input {
      min-width: 220px;
      height: 30px;
      border: 1.5px solid black;
      border-radius: 10px;
      padding: 2px;
      background-color: $main-color;
      font-size: 1em;
      outline: none;
    }
    input:focus{
      border: 2px solid black;
    }

  }
  .checkbox{
    display: inline-flex;
    margin-top: 10px;
    margin-left: 2px;
    input{
      width: 15px;
      height: 15px;
    }
    span{
      margin-left: 10px;
      font-size: 0.9em;
    }
  }

  button{
    width: 100px;
    height: 30px;
    margin-left: 60px;
    margin-top: 10px;
    border: 1.5px solid black;
    border-radius: 10px;
    background-color: $button-color;
    font-size: 1em;
  }

  button:hover{
    border: 2px solid black;
  }

}